import styled from 'styled-components';

const StyledAccount = styled.ul`
  position: absolute;
  top: 0.5rem;
  width: 1000px;
  max-width: 90vw;
  padding: 3rem;
  display: flex;
  flex-direction: column;

  box-shadow: 1px 1px 10px 1px rgba(41, 47, 54, 0.4);
  border-radius: 20px;
  margin: 2rem 1rem;
  background-color: #ffffff;
  list-style-type: none;

  @media only screen and (max-width: 800px) {
    width: 90vw;
    padding: 2rem 1rem;
  }
`;

export default StyledAccount;
